.i {
  display: flex;
  height: 100vh;
  background-image: url(../../resources/forme1.png),
    url(../../resources/motif1.png), url(../../resources/forme2.png);
  background-repeat: no-repeat, no-repeat, no-repeat;
  background-position: top left, top left, center;
}

.i_gauche {
  flex: 1;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: center;
}
.i_titre_wrapper{
    text-align:right;
    
  }
.i_droite {
  flex: 1;
  text-align: left;
  position: relative;
}

.i_gauche_wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i_intro {
  font-size: 35px;
  font-weight: 300;
}

.i_nom {
  font-size: 75px;
  font-weight: 600;
}

.i_title {
  height: 50px;
  overflow: hidden;
}

.i_titre_wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
  
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

.i_title_item {
  height: 50px;
  font-size: 35px;
  font-weight: bold;
  color: #59b256;
  display: flex;
  
  background-image: linear-gradient(to right, #ff37f5 0%, #7d06b4  51%, #d332f3  100%);
  background-clip: text;
    -webkit-text-fill-color: transparent;
}

.i_description {
  font-size: 17px;
}

.i_img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  align-items: flex-start;
}

.i_imgApp {
  width: initial;
  height: initial;
  position: absolute;
  /*animation: bouge 2s ease-in-out infinite alternate;*/
}

/*
@keyframes bouge {
    0% {
        transform: translateY(-30px);
    }
}*/

.i_bg {
  clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background: linear-gradient(
    90deg,
    rgba(52, 218, 238, 1) 0%,
    rgba(18, 236, 83, 1) 100%
  );
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

@media (max-width: 480px) {
  .i {
    flex-direction: column;
  }
  .i_gauche_wrapper {
    padding: 10px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }
  .i_title{
    text-align: center;
  }
}

@media (max-width: 1200px) {
  .i {
    flex-direction: column;
  }
  .i_gauche_wrapper {
    padding: 20px 0px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }
  .i_description {
    padding: 0px 2rem;
    margin-top: 10px;
    text-align: justify;
  }
  .i_title_item{
    display: block;
  }
  .i_titre_wrapper{
    text-align: center;
  }
}

@media (max-width: 1404px) {
  .i_gauche_wrapper {
    padding: 20px 0px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }
  .i_title{
    text-align: center;
  }
  .i_description {
    padding: 0px 2rem;
    
    text-align: justify;
  }
}
