.container {
  height: fit-content;
  background-image: linear-gradient(45deg, #93a5cf81 0%, #e4efe980 100%);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 15px 25px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 10px -8px;
}

.CouleurDate {
  color: rgb(29, 29, 29);
}

.parcours {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background: linear-gradient(
    90deg,
    rgba(52, 218, 238, 1) 50%,
    rgba(18, 236, 83, 1) 100%
  );
  padding: 10px 0px;
  margin-bottom: 5rem;
  text-align: center;
  color: rgb(22, 94, 69);
    
}
