

.pageContainer{
    display: flex;
  flex-wrap: wrap;
    column-gap: 5rem;
    row-gap: 3rem;
    justify-content: space-around;
   
  
    
}


.wrapper{
    padding: 2rem 10rem;
    padding-bottom: 7rem;
    
    
    
}
.competences{

    
    
    color: rgb(22, 94, 69);
    
}
.carte{
    
    flex: 1 0 21%; /* explanation below */
    transition: All 1s ease-in-out;
    height: 100px;
    background-color: rgb(18, 18, 194);
  
}
.carte:hover{
    width: 500px;
    height: 100px;
    
}
.titre{
    text-align: center;
    margin-bottom: 3rem;
    font-size: 50px;
    border: none;
}
@media (max-width: 560px) {
    .wrapper{
        padding: 3rem 3rem;
    }
    .titre{
        width: 50%;
  margin: 0 auto;
  padding-bottom: 2rem;

    }
    
    .titre{

    padding: 0px auto;
    
        width: fit-content;

        
    }
  }
